<template>
  <div class="dialog">
    <el-row :gutter="20">
      <el-col :span="6"
        >【题型】：
        <span v-if="dialogData.questionType === '1'">单选题</span>
        <span v-else-if="dialogData.questionType === '2'">多选题</span>
        <span v-else>简答题</span>
      </el-col>
      <el-col :span="6">【编号】：{{ dialogData.id }}</el-col>
      <el-col :span="6"
        >【难度】：
        <span v-if="dialogData.difficulty === '1'">简单</span>
        <span v-else-if="dialogData.difficulty === '2'">一般</span>
        <span v-else>困难</span>
      </el-col>
      <el-col :span="6">【标签】：{{ dialogData.tags }}</el-col>
      <el-col :span="6"
        >【学科】：{{ dialogData.subjectName || dialogData.subject }}</el-col
      >
      <el-col :span="6"
        >【目录】：{{ dialogData.directoryName || dialogData.catalog }}</el-col
      >
      <el-col :span="6">【方向】：{{ dialogData.direction }}</el-col>
    </el-row>
    <el-divider></el-divider>
    【提干】：
    <div style="color: blue" v-html="dialogData.question"></div>
    <div class="answer">
      <div style="margin-bottom: 5px">
        <span v-if="dialogData.questionType === '1'"
          >单选题 选项：（以下选中的选项为正确答案）
          <div
            v-for="item in dialogData.options"
            :key="item.id"
            style="padding: 8px 0"
          >
            <el-radio
              v-if="item.isRight === 1"
              v-model="radio"
              :label="item.isRight"
              >{{ item.title }}</el-radio
            >
            <el-radio v-else :label="item.isRight">{{ item.title }}</el-radio>
          </div>
        </span>
        <span v-else-if="dialogData.questionType === '2'"
          >多选题 选项：（以下选中的选项为正确答案）
          <div
            v-for="item in dialogData.options"
            :key="item.id"
            style="padding: 8px 0"
          >
            <el-checkbox
              v-if="item.isRight === 1"
              v-model="checked"
              :label="item.isRight"
              >{{ item.title }}</el-checkbox
            >
            <el-checkbox v-else :label="item.isRight">{{
              item.title
            }}</el-checkbox>
          </div>
        </span>
      </div>
    </div>
    <el-divider></el-divider>
    【参考答案】：
    <el-button type="danger" @click="updateVideos">视频答案预览</el-button>
    <div v-if="videos">
      <video controls height="240" style="margin-top: 10px" width="320">
        <source :src="dialogData.videoURL" type="video/mp4" />
      </video>
    </div>
    <el-divider></el-divider>
    【答案解析】：<span
      style="display: inline-block"
      v-html="dialogData.answer === null ? '无' : dialogData.answer"
    ></span>
    <el-divider></el-divider>
    【题目备注】：<span
      style="display: inline-block"
      v-html="dialogData.remarks === null ? '无' : dialogData.remarks"
    ></span>
  </div>
</template>

<script>
export default {
  name: 'quest-dialog',
  props: ['dialogData', 'videos'],
  model: {
    prop: 'videos'
  },
  data () {
    return {
      radio: 1,
      checked: true
    }
  },
  watch: {
    videos (oldValue) {
      this.videos = oldValue
    }
  },
  methods: {
    updateVideos () {
      this.$emit('input', !this.videos)
    }
  }
}
</script>

<style lang="scss" scoped>
.dialog {
  ::v-deep .el-col {
    padding: 10px 0;
    font-size: 17px;
  }

  .answer {
    font-size: 16px;
  }
}
</style>
